import React, {PureComponent} from 'react';
import {connect} from 'react-redux';
import {addShip, handleInputKeyDown, handleInputKeyUp, loopTick} from './api/actions';
import onTickSyncStateToScene from './reducers/onTickSyncStateToScene';
import {initCamera} from './components/camera';
import {
  WebGLRenderer,
  AmbientLight
} from 'three';

import Message from './components/Message';

const LOOP_INTERVALS = 25;
let WIDTH = 600;
let HEIGHT = 600;

class AppComponent extends PureComponent {

  constructor(props) {
    super(props);

    this._loopInterval = null;

    this.state = {
      loopRequest: 0
    };

    this.onButtonClick = this._onButtonClick.bind(this);
    this.onKeyDown = this._onKeyDown.bind(this);
    this.onKeyUp = this._onKeyUp.bind(this);
    this.onLoopTick = this._onLoopTick.bind(this);
  }

  render() {
    return (
      <div className="App">
        <div className="interface">
          <div className="top-bar">
            <button className="menu-button" onClick={() =>
              this.onButtonClick()}>
              Add Ship
            </button>
          </div>
          <div id="canvasContainer">
          </div>
          <div className="bottom-bar">
            <div>FPS: {this.props.gameLoop.get('fps')}</div>
            <div>{this._renderMessages()}</div>
          </div>
        </div>
      </div>
    );
  }

  _renderMessages() {
    return this.props.gameLoop
      .getIn(['messages', 'list'])
      .map((message, index) =>
        <Message key={message.get('id')} data={message}/>
      )
  }

  _onButtonClick() {
    this.props.onButtonClick()
  }

  _onKeyDown(event) {
    this.props.onKeyDown(event.key);
  }

  _onKeyUp(event) {
    this.props.onKeyUp(event.key);
  }

  _onLoopTick() {
    this.props.onLoopTick();

    // update scene with state
    onTickSyncStateToScene(this.props.gameLoop);

    this._renderer.render(this.props.gameLoop.get('scene'), this._camera);
    this._loopInterval = window.setTimeout(this.onLoopTick, LOOP_INTERVALS);
  }

  componentDidMount() {
    window.addEventListener("keydown", this.onKeyDown);
    window.addEventListener("keyup", this.onKeyUp);
    this._loopInterval = window.setTimeout(this.onLoopTick, LOOP_INTERVALS);

    this._initScene();
    document.getElementById('canvasContainer').appendChild(this._renderer.domElement);
  }

  _initScene() {
    const {gameLoop} = this.props;
    this._renderer = this._initRenderer();
    this._camera = initCamera(gameLoop.get('camera'));
    this._lights = this._initLights(gameLoop.get('scene'), gameLoop.get('lights'));
  }

  componentWillUnmount() {
    window.removeEventListener("keydown", this.onKeyDown);
    window.removeEventListener("keyup", this.onKeyUp);
    window.clearTimeout(this._loopInterval);
    this._loopInterval = null;
  }

  _initRenderer() {
    const renderer = new WebGLRenderer({antialias: true});
    renderer.setSize(WIDTH, HEIGHT);
    return renderer;
  }

  _initLights(scene) {
    let light = new AmbientLight(0xffffff);
    scene.add(light);
    return light;
  }
}

// move these to separate files:

AppComponent.propTypes = {
  count: React.PropTypes.number,
  gameLoop: React.PropTypes.object
};


const mapStateToProps = (state) => {
  return {
    count: state.base.count,
    gameLoop: state.gameLoop
  }
};

const mapDispatchToProps = (dispatch) => {
  return {
    onButtonClick: () => {
      dispatch(addShip())
    },
    onKeyDown: (key) => {
      dispatch(handleInputKeyDown(key));
    },
    onKeyUp: (key) => {
      dispatch(handleInputKeyUp(key));
    },
    onLoopTick: () => {
      dispatch(loopTick());
    }
  }
};

const App = connect(
  mapStateToProps,
  mapDispatchToProps
)(AppComponent);

export default App;


