import {Map, List} from 'immutable';
import {Scene} from 'three';
import id from './api/idGenerator';
import shipJson from '../assets/meshes/ship.json';

export const SPEED = 0.1;
let WIDTH = window.innerWidth;
let HEIGHT = window.innerHeight;

const initialState = new Map({
  scene: new Scene(),
  camera: new Map({
    fieldOfView: 1000,
    aspect: WIDTH / HEIGHT,
    near: 1,
    far: 100,
    position: new Map({
      x: 0,
      y: 0,
      z: 25
    }),
    rotation: new Map({
      x: 0,
      y: 0,
      z: 0
    }),
    lookAt: new Map({
      x: 0,
      y: 0,
      z: 0
    })
  }),
  ship: new Map({
    id: id(),
    height: 40,
    width: 40,
    rotation: new Map({
      x: -4.71,
      y: 4.71,
      z: 0
    }),
    position: new Map({
      x: 0,
      y: 0,
      z: 0
    }),
    meshData: new Map({
      scale: 0.25
    }),
    jsonData: shipJson
  }),
  ships: new Map({
    count: 3,
    list: new List([new Map({
      id: id(),
      height: 40,
      width: 40,
      rotation: new Map({
        x: -4.71,
        y: 4.71,
        z: 0
      }),
      position: new Map({
        x: 0,
        y: -2,
        z: 0
      }),
      meshData: new Map({
        scale: 0.25
      }),
      jsonData: shipJson
    }), new Map({
      id: id(),
      height: 40,
      width: 40,
      rotation: new Map({
        x: -4.71,
        y: 4.71,
        z: 0
      }),
      position: new Map({
        x: 2,
        y: 0,
        z: 0
      }),
      meshData: new Map({
        scale: 0.25
      }),
      jsonData: shipJson
    }), new Map({
      id: id(),
      height: 40,
      width: 40,
      rotation: new Map({
        x: -4.71,
        y: 4.71,
        z: 0
      }),
      position: new Map({
        x: 0,
        y: 2,
        z: 0
      }),
      meshData: new Map({
        scale: 0.25
      }),
      jsonData: shipJson
    })])
  }),
  keysDown: new Map(),
  projectiles: new Map({
    count: 0,
    list: new List()
  }),
  collisions: new List(),
  planets: new List([new Map({
    height: 120,
    width: 120,
    positionX: 400,
    positionY: 400,
    type: 'blue',
    name: 'Scorpion',
    id: 1
  })]),
  messages: new Map({
    count: 1,
    list: new List([new Map({
      text: 'Welcome to the game; use WASD to fly, spacebar to shoot and L to land on planets',
      id: 1,
      ticks: 200
    })])
  }),
  ticks: 0,
  fps: 60
});

export default initialState;


