import {
  PerspectiveCamera,
  Vector3
} from 'three';

export function initCamera(cameraData) {
  const camera = new PerspectiveCamera(
    cameraData.get('fieldOfView'),
    cameraData.get('aspect'),
    cameraData.get('near'),
    cameraData.get('far'));
  camera.rotation.set(
    cameraData.getIn(['rotation', 'x']),
    cameraData.getIn(['rotation', 'y']),
    cameraData.getIn(['rotation', 'z']));
  camera.position.set(
    cameraData.getIn(['position', 'x']),
    cameraData.getIn(['position', 'y']),
    cameraData.getIn(['position', 'z']));
  camera.lookAt(
    new Vector3(cameraData.getIn(['lookAt', 'x']),
      cameraData.getIn(['lookAt', 'y']),
      cameraData.getIn(['lookAt', 'z'])));
  return camera;
}



