import {
  Mesh,
  MeshPhongMaterial,
  EdgesGeometry,
  LineBasicMaterial,
  LineSegments,
  JSONLoader
} from 'three';

const _JSONLoader = new JSONLoader();

export default function initMesh(scene, meshProps) {
  if (!scene) return;
  const shipMeshData = meshProps.get('meshData');
  const jsonData = meshProps.get('jsonData');
  const {geometry, materials} = _JSONLoader.parse(jsonData);
  let material = new MeshPhongMaterial(materials[0]);
  let mesh = new Mesh(geometry, material);
  mesh.scale.x = mesh.scale.y = mesh.scale.z = shipMeshData.get('scale');
  mesh.translation = geometry.center();
  mesh.position.x = meshProps.getIn(['position', 'x']);
  mesh.position.y = meshProps.getIn(['position', 'y']);
  mesh.position.z = meshProps.getIn(['position', 'z']);
  mesh.rotation.x = meshProps.getIn(['rotation', 'x']);
  mesh.rotation.y = meshProps.getIn(['rotation', 'y']);
  mesh.rotation.z = meshProps.getIn(['rotation', 'z']);
  mesh.name = meshProps.get('id');
  scene.add(mesh);

  // wireframe
  let geo = new EdgesGeometry(mesh.geometry); // or WireframeGeometry
  let mat = new LineBasicMaterial({color: 0x000000, linewidth: 2});
  let wireframe = new LineSegments(geo, mat);
  mesh.add(wireframe);

  return mesh;
}


