import {SPEED} from '../initialState';

export function calculatePositionChange(distance, rotation, position) {
  // console.log(`${distance}`);
  const rotationRadians = rotation.y;
  // const rotationRadians = yRotation === 0 ? 0 : Math.PI * yRotation / 180;

  const {x, y, z} = position;
  const distanceY = SPEED * distance * Math.sin(rotationRadians);
  const distanceX = SPEED * distance * Math.cos(rotationRadians);
  const distanceZ = 0;
  // console.log(`rotation: ${rotationRadians}, x dist: ${distanceX}, y dist: ${distanceY}`);
  return {
    x: x + distanceX,
    y: y + distanceY,
    z: z + distanceZ
  }
}

export function calculateShipPosition(ship, speed) {
  // dy = d * cos(rotation)
  // dx = d * sin(rotation)
  // could the compute cos and sin of angles up front in a map:
  // rotation -> {cos(rotation), sin(rotation)}
  const {x, y, z} = calculatePositionChange(
    speed, {
      x: ship.getIn(['rotation', 'x']),
      y: ship.getIn(['rotation', 'y']),
      z: ship.getIn(['rotation', 'z'])
    }, {
      x: ship.getIn(['position', 'x']),
      y: ship.getIn(['position', 'y']),
      z: ship.getIn(['position', 'z'])
    });
  let updatedState = ship
    .setIn(['position', 'x'], x)
    .setIn(['position', 'y'], y)
    .setIn(['position', 'z'], z);
  // console.log(`rotation: ${updatedState.get('rotation')}, position: ${updatedState.get('position')}`)
  return updatedState;
}


