import id from '../api/idGenerator';

export function addShip(state) {
  const updatedCount = state.getIn(['ships', 'count']) + 1;
  const updatedState = state.setIn(['ships', 'count'], updatedCount);
  const shipWithId = action.ship.set('id', updatedCount);
  updatedState.setIn(['ships', 'list'],
    updatedState.getIn(['ships', 'list']).push(shipWithId));
  return state;
}


