import {Map, List} from 'immutable';

import addMessage from './addMessage';

function resolveCollision(state, collision) {
  if (!collision) return state;
  const projectile = collision.get('projectile');
  const projectileId = projectile.get('id');
  const ship = collision.get('ship');
  const shipId = ship.get('id');
  // console.log(`resolving collision: projectile: ${projectile}, ship: ${ship}`);
  const projectileIndex = state.getIn(['projectiles', 'list']).findIndex((projectile) => {
    return projectile.get('id') == projectileId;
  });
  const shipIndex = state.getIn(['ships', 'list']).findIndex((ship) => {
    return ship.get('id') == shipId;
  });
  const stateWithoutProjectile = state.deleteIn(['projectiles', 'list', projectileIndex]);
  const stateWithoutShip = stateWithoutProjectile.deleteIn(['ships', 'list', shipIndex]);
  return stateWithoutShip;
}

export function collisionResolver(state) {
  const collisions = state.get('collisions');
  if (collisions.size > 0) {
    const stateWithResolvedCollisions = collisions.reduce((updatedState, collision) => {
      const message = {
        text: 'Ship was destroyed',
        ticks: 100
      };
      const stateWithMessage = addMessage(updatedState, message);
      // console.log(`adding message: ${message.text}`);
      return resolveCollision(stateWithMessage, collision);
    }, state);
    return stateWithResolvedCollisions.set('collisions', new List());
  }
  return state;
}


