let startTimeMilliSeconds = Math.trunc(new Date().valueOf());

const MAX_SAMPLING_TIME_MS_DELTA = 3000;
const MIN_FRAMES_PER_SEC = 25;
const MIN_FRAMES_PER_SAMPLE = MIN_FRAMES_PER_SEC * MAX_SAMPLING_TIME_MS_DELTA / 1000;

export function onTickFPS(state) {
  const ticks = state.get('ticks');
  const newTimeSeconds = Math.trunc(new Date().valueOf());
  const timeDelta = newTimeSeconds - startTimeMilliSeconds;
  let updatedState = state;
  if (timeDelta >= MAX_SAMPLING_TIME_MS_DELTA) {
    if (ticks < MIN_FRAMES_PER_SAMPLE) console.warn(`ticks are low at ${MAX_SAMPLING_TIME_MS_DELTA / 1000} seconds: ${ticks}`);
    startTimeMilliSeconds = newTimeSeconds;
    updatedState = state.set('ticks', 0);
  }
  const avgFps = Math.trunc(1000 * ticks / timeDelta);
  return updatedState
    .set('ticks', updatedState.get('ticks') + 1)
    .set('fps', avgFps);
}


