import {Map} from 'immutable';

export default function addMessage(state, message) {
  const {text, ticks} = message;
  const messages = state.get('messages');
  const count = messages.get('count');
  const list = messages.get('list');
  const updatedMsgList = list.push(new Map({
    id: count + 1,
    text, ticks
  }));
  return state.setIn(['messages', 'list'], updatedMsgList)
    .setIn(['messages', 'count'], count + 1);
}


