import initMesh from '../initMesh';

function updateProjectiles(scene, projectilesState) {
  if (!scene) return;

  projectilesState.get('list').forEach((projectileState) => {
    const id = projectileState.get('id');
    let projectile = scene.getObjectByName(id);
    if (!projectile) {
      projectile = initMesh(scene, projectileState);
    }
    projectile.position.x = projectileState.getIn(['position', 'x']);
    projectile.position.y = projectileState.getIn(['position', 'y']);
    projectile.position.z = projectileState.getIn(['position', 'z']);
    projectile.rotation.x = projectileState.getIn(['rotation', 'x']);
    projectile.rotation.y = projectileState.getIn(['rotation', 'y']);
    projectile.rotation.z = projectileState.getIn(['rotation', 'z']);
  });
}

// TODO: figure out a strategy for dealing with scene updates and encapsulation vs separation of responsibilities
function updateShip(scene, shipState) {
  if (!scene) return;
  let ship = scene.getObjectByName(shipState.get('id'));
  if (!ship) {
    ship = initMesh(scene, shipState);
  }
  ship.position.x = shipState.getIn(['position', 'x']);
  ship.position.y = shipState.getIn(['position', 'y']);
  ship.position.z = shipState.getIn(['position', 'z']);
  ship.rotation.x = shipState.getIn(['rotation', 'x']);
  ship.rotation.y = shipState.getIn(['rotation', 'y']);
  ship.rotation.z = shipState.getIn(['rotation', 'z']);
}

function updateShips(scene, shipsState) {
  if(!scene) return;

  shipsState.get('list').forEach((ship) => {
    updateShip(scene, ship);
  });
}

export default function onTickSyncStateToScene(state) {
  updateShip(state.get('scene'), state.get('ship'));
  updateShips(state.get('scene'), state.get('ships'));
  updateProjectiles(state.get('scene'), state.get('projectiles'));
}


