import {calculatePositionChange} from './movement';

function filterVisibleProjectiles(projectile, index, projectiles) {
  const position = projectile.get('position');
  const positionX = position.get('x');
  const positionY = position.get('y');
  // console.log(`filtering: (${positionX}, ${positionY})`);
  return !(positionX > 100 || positionX < -100 || positionY > 100 || positionY < -100);
}

export function onTickProjectiles(state) {
  const projectiles = state.getIn(['projectiles', 'list']);
  const projectilesAfterPruningOffscreen = projectiles.filter(filterVisibleProjectiles);
  const stateWithVisibleProjectilesOnly = state.setIn(['projectiles', 'list'], projectilesAfterPruningOffscreen);

  const stateWithProjectilePositionChanges = projectilesAfterPruningOffscreen
    .reduce((updatedState, projectile, index) => {
      const {x, y, z} = calculatePositionChange(
        projectile.get('speed'), {
          x: projectile.getIn(['rotation', 'x']),
          y: projectile.getIn(['rotation', 'y']),
          z: projectile.getIn(['rotation', 'z'])
        }, {
          x: projectile.getIn(['position', 'x']),
          y: projectile.getIn(['position', 'y']),
          z: projectile.getIn(['position', 'z'])
        });

      let updatedProjectile = projectile
        .setIn(['position', 'x'], x)
        .setIn(['position', 'y'], y)
        .setIn(['position', 'z'], z);
      return updatedState.setIn(['projectiles', 'list', index], updatedProjectile)
    }, stateWithVisibleProjectilesOnly);

  return stateWithProjectilePositionChanges;
}


