import {Map, List} from 'immutable';
import projectileJson from '../../assets/meshes/projectile.json';
import id from '../api/idGenerator';

export function fireWeapon(state) {
  const ship = state.get('ship');
  const rotation = ship.get('rotation'); // comes out same rotation as ship
  const position = ship.get('position');
  const updatedCount = state.getIn(['projectiles', 'count']) + 1;

  const newProjectileProps = new Map({
    rotation, position,
    height: 40,
    width: 40,
    speed: 5,
    id: id(),
    jsonData: projectileJson,
    meshData: new Map({
      scale: 0.25
    })
  });

  const currentProjectiles = state.getIn(['projectiles', 'list']);
  const updatedProjectiles = currentProjectiles.push(newProjectileProps);
  const updatedState =  state.setIn(['projectiles', 'count'], updatedCount);
  return updatedState.setIn(['projectiles', 'list'], updatedProjectiles);
}


